import React, { memo, useState } from 'react';
import gql from 'graphql-tag';
import { Formik, Field } from 'formik';
import styled from 'styled-components';
import { Mutation } from 'react-apollo';
import TextInput from '../inputs/TextInput';
import PasswordInput from '../inputs/PasswordInput';
import Button from '../styled/Button';
import { FieldHeading } from '../styled/Typography';
import Wrapper from './Wrapper';
import { passwordUpdateValidation } from '../../utils/validationSchema';
import LoadingIcon from '../shared/LoadingIcon';
import ServerErrorMessage from '../inputs/ServerErrorMessage';

const AccountSettings = ({ id, status, setStatus }) => {
  const [toggled, setToggled] = useState(false);

  const handleToggle = () => setToggled(!toggled);

  return (
    <Wrapper
      heading="Account Settings"
      copy="Update your email address, mailing address, and/or password."
      status={status}
      setStatus={setStatus}
    >
      <FieldTitle>ACCOUNT EMAIL</FieldTitle>
      <Field name="email">
        {({ field, form }) => (
          <TextInput
            {...field}
            label="Email Address"
            placeholder="Enter email address"
            error={form.errors.email}
            touched={form.touched.email}
          />
        )}
      </Field>
      <FieldTitle>PASSWORD</FieldTitle>
      {!toggled && (
        <PasswordButton onClick={handleToggle}>Change Password</PasswordButton>
      )}
      {toggled && (
        <Mutation mutation={PASSWORD_UPDATE}>
          {(updatePassword, { error }) => (
            <Formik
              initialValues={{
                currentPassword: '',
                newPassword: '',
                confirmNewPassword: '',
              }}
              validate={passwordUpdateValidation}
              onSubmit={async (values, actions) => {
                try {
                  const { data } = await updatePassword({
                    variables: {
                      id: +id,
                      currentPassword: values.currentPassword,
                      newPassword: values.newPassword,
                    },
                  });
                  if (data.passwordUpdate) {
                    handleToggle();
                  }
                  actions.setSubmitting(false);
                  setStatus({ submitted: true });
                } catch (err) {
                  actions.setSubmitting(false);
                }
              }}
              validateOnChange={false}
              validateOnBlur={false}
            >
              {passwordUpdateForm => (
                <>
                  {!passwordUpdateForm.isSubmitting && (
                    <ServerErrorMessage visible={error} error={error} />
                  )}
                  <Field name="currentPassword">
                    {({ field, form }) => (
                      <PasswordInput
                        {...field}
                        label="Current Password"
                        placeholder="Enter current password"
                        error={form.errors.currentPassword}
                        touched={form.touched.currentPassword}
                      />
                    )}
                  </Field>
                  <Field name="newPassword">
                    {({ field, form }) => (
                      <PasswordInput
                        {...field}
                        label="New Password"
                        placeholder="Enter new password"
                        error={form.errors.newPassword}
                        touched={form.touched.newPassword}
                      />
                    )}
                  </Field>
                  <Field name="confirmNewPassword">
                    {({ field, form }) => (
                      <PasswordInput
                        {...field}
                        label="Confirm New Password"
                        placeholder="Confirm new password"
                        error={form.errors.confirmNewPassword}
                        touched={form.touched.confirmNewPassword}
                        height={36}
                      />
                    )}
                  </Field>
                  <ButtonContainer>
                    <CancelButton
                      secondary
                      onClick={() => {
                        handleToggle();
                      }}
                    >
                      Cancel
                    </CancelButton>
                    <UpdateButton onClick={passwordUpdateForm.handleSubmit}>
                      {!passwordUpdateForm.isSubmitting ? (
                        'Update'
                      ) : (
                        <LoadingIcon />
                      )}
                    </UpdateButton>
                  </ButtonContainer>
                </>
              )}
            </Formik>
          )}
        </Mutation>
      )}
      <FieldTitle>Current Address</FieldTitle>
      {/* <SelectInput label="Country" placeholder="United States" /> */}
      <Field name="streetAddress1">
        {({ field, form }) => (
          <TextInput
            {...field}
            label="Street Address 1"
            placeholder="Street address"
            error={form.errors.streetAddress1}
            touched={form.touched.streetAddress1}
          />
        )}
      </Field>
      <Field name="streetAddress2">
        {({ field, form }) => (
          <TextInput
            {...field}
            label="Street Address 2"
            placeholder="Apt number, unit, building, floor"
            error={form.errors.streetAddress2}
            touched={form.touched.streetAddress2}
          />
        )}
      </Field>
      <Field name="city">
        {({ field, form }) => (
          <TextInput
            {...field}
            label="City"
            placeholder="Enter city"
            error={form.errors.city}
            touched={form.touched.city}
          />
        )}
      </Field>
      <Field name="state">
        {({ field, form }) => (
          <TextInput
            {...field}
            label="State/Territory"
            placeholder="Select state or territory"
            error={form.errors.state}
            touched={form.touched.state}
          />
        )}
      </Field>
      <Field name="postalCode">
        {({ field, form }) => (
          <TextInput
            {...field}
            label="Postal Code"
            placeholder="YYYYY"
            error={form.errors.postalCode}
            touched={form.touched.postalCode}
          />
        )}
      </Field>
    </Wrapper>
  );
};

export default memo(AccountSettings);

const FieldTitle = styled(FieldHeading)`
  margin-top: 3.125rem;
`;

const PasswordButton = styled(Button)`
  margin: 0;
  width: 10.75rem;
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: flex-start;
`;

const CancelButton = styled(Button)`
  margin: 0 0.625rem 0 0;
`;

const UpdateButton = styled(Button)`
  margin: 0;
`;

const PASSWORD_UPDATE = gql`
  mutation passwordUpdate(
    $id: Int!
    $currentPassword: String!
    $newPassword: String!
  ) {
    passwordUpdate(
      id: $id
      currentPassword: $currentPassword
      newPassword: $newPassword
    ) {
      id
      email
    }
  }
`;
