import React from 'react';
import styled from 'styled-components';
import { Transition, animated } from 'react-spring';
import ClearIcon from '../icons/ClearIcon';

const Success = ({ visible, message, className, setStatus }) => (
  <Transition
    native
    items={visible || false}
    from={{ h: 0, o: 0 }}
    enter={{ h: 136, o: 1 }}
    leave={{ h: 0, o: 0 }}
  >
    {show =>
      show &&
      (({ h, o }) => (
        <animated.div
          style={{
            height: h.interpolate(h => h),
            opacity: o.interpolate(o => o),
          }}
        >
          <Base className={className}>
            <Container>
              <Box>
                {message}{' '}
                <Clear onClick={() => setStatus({ submitted: false })} />
              </Box>
            </Container>
          </Base>
        </animated.div>
      ))
    }
  </Transition>
);

export default Success;

const Base = styled.div`
  overflow: hidden;
  position: relative;
  top: 0.25rem;
  width: 100%;
`;

const Container = styled.div`
  padding: 3.75rem 0 1.625rem;
`;

const Box = styled.div`
  align-items: center;
  background: rgba(88, 207, 0, 0.08);
  border: 1px #58cf00 solid;
  color: #58cf00;
  display: flex;
  font-size: 0.875rem;
  height: 3.125rem;
  line-height: 125%;
  padding: 0 1.25rem;
  width: 100%;
`;

const Clear = styled(ClearIcon)`
  cursor: pointer;
  margin-left: auto;
  stroke: #58cf00;
`;
