import React, { memo } from 'react';
import styled from 'styled-components';
import { BodyText, Heading } from '../styled/Typography';
import { viewports } from '../../utils/variables';
import Success from './Success';

const Wrapper = ({ children, heading, copy, status, setStatus }) => (
  <Container>
    <Success
      message="Your changes have been saved!"
      visible={status && status.submitted}
      setStatus={setStatus}
    />
    <SectionHeading>{heading}</SectionHeading>
    <Copy>{copy}</Copy>
    <Fields>{children}</Fields>
  </Container>
);

export default memo(Wrapper);

const Container = styled.div`
  padding: 0 1.875rem;
  width: 100%;

  @media ${viewports.medium} {
    margin: 0 auto;
    padding: 0;
    width: 32.5rem;
  }
`;

const SectionHeading = styled(Heading)`
  padding-top: 3.125rem;
`;

const Copy = styled(BodyText)`
  margin-bottom: 1.875rem;
`;

const Fields = styled.div`
  border-top: 2px #f4f4f4 solid;
`;
