import React, { memo } from 'react';
import styled from 'styled-components';
import { Field } from 'formik';
import { fontFamilies, viewports } from '../../utils/variables';
import TextInput from '../inputs/TextInput';
import SelectInput from '../inputs/SelectInput';
import TextArea from '../inputs/TextAreaInput';
import { FieldHeading, Label } from '../styled/Typography';
import Wrapper from './Wrapper';
import { industries } from '../../utils/selectOptions';
import Toggle from '../shared/Toggle';
import ImageUploadInput from '../inputs/ImageUploadInput';

const Profile = ({ values, status, setStatus }) => (
  <Wrapper
    heading="Profile"
    copy="Edit your photo, bio, contact information and social media links."
    status={status}
    setStatus={setStatus}
  >
    <Title>EDUCATION</Title>
    <FieldWrapper>
      <FieldKey>Major</FieldKey>
      <FieldInfo>{values.major}</FieldInfo>
    </FieldWrapper>
    <FieldWrapper>
      <FieldKey>Class Year</FieldKey>
      <FieldInfo>{values.graduationYear}</FieldInfo>
    </FieldWrapper>
    <FieldCopy>
      If your education information is incorrect, contact ArtCenter at
      alumni@artcenter.edu.
    </FieldCopy>
    <Title>Photo</Title>
    <Field name="profileImage">
      {({ field }) => (
        <ImageUploadInput
          {...field}
          label="Photo"
          uploadCta="Upload/Change Photo"
        />
      )}
    </Field>
    <Title>Career</Title>
    <Field name="currentCompany">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={values.visibleFields.currentCompany}
          form={form}
          label="Current Company"
          placeholder="Enter company name"
          error={form.errors.currentCompany}
          touched={form.touched.currentCompany}
        />
      )}
    </Field>
    <Field name="currentJobTitle">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={values.visibleFields.currentJobTitle}
          form={form}
          label="Current Job Title"
          placeholder="Select the best match"
          error={form.errors.currentJobTitle}
          touched={form.touched.currentJobTitle}
        />
      )}
    </Field>
    <Field name="currentIndustry">
      {({ field, form }) => (
        <SelectInput
          {...field}
          toggle
          toggled={values.visibleFields.currentIndustry}
          form={form}
          label="Current Job Industry"
          placeholder="Select the best match"
          options={industries}
          error={form.errors.currentIndustry}
          touched={form.touched.currentIndustry}
        />
      )}
    </Field>
    <Title>Contact</Title>
    <Field name="email">
      {({ field, form }) => (
        <>
          <Label htmlFor={field.name}>Email Address</Label>
          <StaticInput>
            <StaticValue>{form.values.email}</StaticValue>
            <Toggle
              form={form}
              toggled={values.visibleFields.email}
              name={field.name}
            />
          </StaticInput>
        </>
      )}
    </Field>
    {/* <Field name="">
      {({ field }) => <TextInput {...field} toggle toggled={values.visibleFields.} label="Phone Number" />}
    </Field> */}
    <Title>SOCIAL MEDIA / WEBSITE</Title>
    <Field name="website">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={values.visibleFields.website}
          form={form}
          label="Website"
          placeholder="Enter website url"
        />
      )}
    </Field>
    <Field name="linkedIn">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={values.visibleFields.linkedIn}
          form={form}
          label="LinkedIn"
          placeholder="Add LinkedIn url"
        />
      )}
    </Field>
    <Field name="instagram">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={values.visibleFields.instagram}
          form={form}
          label="Instagram"
          placeholder="Add Instagram url"
        />
      )}
    </Field>
    <Field name="facebook">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={values.visibleFields.facebook}
          form={form}
          label="Facebook"
          placeholder="Add Facebook url"
        />
      )}
    </Field>
    <Field name="twitter">
      {({ field, form }) => (
        <TextInput
          {...field}
          toggle
          toggled={values.visibleFields.twitter}
          form={form}
          label="Twitter"
          placeholder="Add Twitter url"
        />
      )}
    </Field>
    <Field name="bio">
      {({ field, form }) => (
        <TextArea
          toggle
          {...field}
          toggled={form.values.visibleFields.bio}
          form={form}
          label="Short Bio"
          totalCharacters={350}
          placeholder="Tell people about yourself."
        />
      )}
    </Field>
  </Wrapper>
);

export default memo(Profile);

const Title = styled(FieldHeading)`
  margin-top: 3.125rem;
`;

const FieldWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  font-family: ${fontFamilies.medium};
  font-size: 0.875rem;
  line-height: 18px;
  margin-bottom: 0.625rem;
`;

const FieldKey = styled.div`
  color: #222222;
  width: 7rem;
`;

const FieldInfo = styled.div`
  color: #777777;
  font-family: ${fontFamilies.regular};
  font-size: 0.875rem;
  line-height: 18px;
`;

const FieldCopy = styled.p`
  color: #999999;
  font-size: 0.75rem;
  line-height: 125%;
  margin-top: 1.25rem;
`;

const StaticInput = styled.div`
  align-items: center;
  margin-bottom: 1.875rem;

  @media ${viewports.medium} {
    display: flex;
  }
`;

const StaticValue = styled.div`
  align-items: center;
  color: #777777;
  display: flex;
  font-family: ${fontFamilies.regular};
  font-size: 0.875rem;
  height: 2.5rem;
  justify-content: flex-start;
  padding-right: 1.25rem;
  width: 100%;
`;
