import React, { memo } from 'react';
import styled from 'styled-components';
import CloseButton from '../shared/CloseButton';
import { fontFamilies, colors, viewports } from '../../utils/variables';

const Nav = ({ activeScreen, exitSettings, setActiveScreen, setStatus }) => (
  <Base>
    <NavItems>
      <NavItem
        active={activeScreen === 'account-settings'}
        onClick={() => {
          setActiveScreen('account-settings');
          setStatus({ submitted: false });
        }}
      >
        Account Settings
      </NavItem>
      <NavItem
        active={activeScreen === 'profile-settings'}
        onClick={() => {
          setActiveScreen('profile-settings');
          setStatus({ submitted: false });
        }}
      >
        Profile
      </NavItem>
    </NavItems>
    <CloseLink onClick={exitSettings} />
  </Base>
);

export default memo(Nav);

const Base = styled.header`
  background-color: #f8f8f8;
  display: flex;
  height: 5.9375rem;
`;

const NavItems = styled.nav`
  display: flex;
  padding-top: 3.5rem;

  @media ${viewports.medium} {
    margin: 0 auto;
    width: 32.5rem;
  }
`;

const NavItem = styled.a`
  color: ${colors.linkBlack};
  cursor: pointer;
  font-family: ${fontFamilies.bold};
  height: 100%;
  margin-left: 1.875rem;
  margin-right: 3.75rem;
  position: relative;

  &:last-of-type {
    margin-left: 0;
    margin-right: 0;
  }

  @media ${viewports.medium} {
    margin-left: 0;
  }

  &:after {
    background: ${colors.red};
    content: '';
    display: block;
    height: 0.25rem;
    left: 0;
    opacity: ${props => (props.active ? '1' : '0')};
    position: absolute;
    width: 100%;

    @media ${viewports.medium} {
      bottom: 0;
    }
  }
`;

const CloseLink = styled(CloseButton)`
  cursor: pointer;
  height: 0.8125rem;
  position: absolute;
  right: 1.9375rem;
  top: 1.9375rem;
  width: 0.8125rem;

  @media ${viewports.medium} {
    right: 2.5rem;
    top: 2.5rem;
  }
`;
