import React, { memo } from 'react';
import styled from 'styled-components';
import Button from '../styled/Button';
import SubmitButton from '../shared/SubmitButton';

const SettingsConfirmation = ({ setActiveScreen, submitting }) => (
  <Base>
    <CancelButton secondary onClick={() => setActiveScreen(undefined)}>
      Cancel
    </CancelButton>
    <ConfirmButton submitting={submitting}>Save Changes</ConfirmButton>
  </Base>
);

export default memo(SettingsConfirmation);

const Base = styled.div`
  align-items: center;
  background: #fff;
  border-top: 1px solid #f2f2f2;
  display: flex;
  justify-content: center;
  margin-top: auto;
  padding: 0.625rem 0;
`;

const CancelButton = styled(Button)`
  margin-right: 0.5rem;
`;

const ConfirmButton = styled(SubmitButton)`
  margin-left: 0.5rem;
`;
