import React, { memo, useRef, useEffect } from 'react';
import { Scrollbars } from 'react-custom-scrollbars';
import { disableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock';
import gql from 'graphql-tag';
import styled from 'styled-components';
import { Mutation } from 'react-apollo';
import { Formik, Form } from 'formik';
import Nav from './Nav';
import SettingsConfirmation from './SettingsConfirmation';
import Profile from './Profile';
import AccountSettings from './AccountSettings';
import { profileUpdateValidation } from '../../utils/validationSchema';

const ProfileSettings = ({ setActiveScreen, activeScreen, user }) => {
  const overlayRef = useRef(null);

  useEffect(() => {
    disableBodyScroll(overlayRef.current);
    return () => clearAllBodyScrollLocks();
  });

  return (
    <Mutation mutation={PROFILE_UPDATE}>
      {updateProfile => (
        <Formik
          initialValues={{
            ...user,
            currentIndustry: {
              label: user.currentIndustry,
              value: user.currentIndustry,
            },
          }}
          validate={profileUpdateValidation}
          onSubmit={async (values, { setSubmitting, setStatus }) => {
            setStatus({ submitted: false });
            try {
              const { data } = await updateProfile({
                variables: {
                  ...values,
                  id: +user.id,
                },
              });
              if (data) {
                setStatus({ submitted: true });
                setSubmitting(false);
              }
            } catch (err) {
              console.error(err);
            }
          }}
        >
          {({ values, isSubmitting, status, setStatus }) => (
            <Base>
              <Scrollbars autoHide>
                <Nav
                  activeScreen={activeScreen}
                  exitSettings={() => setActiveScreen(undefined)}
                  setActiveScreen={setActiveScreen}
                  setStatus={setStatus}
                />
                {activeScreen === 'account-settings' && (
                  <AccountSettings
                    id={user.id}
                    status={status}
                    setStatus={setStatus}
                  />
                )}
                {activeScreen === 'profile-settings' && (
                  <Profile
                    values={values}
                    status={status}
                    setStatus={setStatus}
                  />
                )}
                <SettingsConfirmation
                  setActiveScreen={setActiveScreen}
                  submitting={isSubmitting}
                />
              </Scrollbars>
            </Base>
          )}
        </Formik>
      )}
    </Mutation>
  );
};

export default memo(ProfileSettings);

const Base = styled(Form)`
  background: #fff;
  bottom: 0;
  display: flex;
  flex-direction: column;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 10;
`;

export const PROFILE_UPDATE = gql`
  mutation profileUpdate(
    $id: Int!
    $email: String
    $streetAddress1: String
    $streetAddress2: String
    $city: String
    $state: String
    $postalCode: String
    $currentCompany: String
    $currentJobTitle: String
    $currentIndustry: SelectInput
    $website: String
    $linkedIn: String
    $instagram: String
    $facebook: String
    $twitter: String
    $profileImage: String
    $bio: String
    $visibleFields: VisibleFieldsInput
  ) {
    profileUpdate(
      id: $id
      email: $email
      streetAddress1: $streetAddress1
      streetAddress2: $streetAddress2
      city: $city
      state: $state
      postalCode: $postalCode
      currentCompany: $currentCompany
      currentJobTitle: $currentJobTitle
      currentIndustry: $currentIndustry
      website: $website
      linkedIn: $linkedIn
      instagram: $instagram
      facebook: $facebook
      twitter: $twitter
      profileImage: $profileImage
      bio: $bio
      visibleFields: $visibleFields
    ) {
      id
      email
    }
  }
`;
